#Works{
    margin: 0 auto;
    min-height: calc(100vh - 4rem);
    width: 100vw;
    max-width: 60rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 0 1rem;
   
}
.workstitle{
    margin: 1rem 0;
    font-size: 3rem;

}
.worksDesc{
    font-weight: 300;
    font-size: 1rem;
    max-width: 45rem;
}
.worksImgs{
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100vw;
    max-width: 65rem;
    flex-wrap: wrap;
}
.worksImg{
    object-fit: cover;
    height: 20rem;
    margin: 0.5rem;
    border: 5px solid grey;
    border-radius: 8px;
}
.worksBtn{
    margin: 2rem 0;
    padding: 0.5rem 2.5rem;
    background-color: rgb(247, 49, 49);
   border: none;
   font-size: 1rem;
   border-radius: 2rem;


}
@media screen and (max-width:585px){
    .worksImg{
        height: 48vw;
    }
}